.header {
  padding: 20px 0 0;
  position: absolute;
  left: 0;
  top: 0;
  right: 0;
  margin: auto;
  z-index: 2;
}
.header__wrap {
  display: grid;
  grid-template-columns: 323px auto 188px;
  position: relative;
}
.header .logos {
  align-self: center;
  display: flex;
  align-items: flex-start;
}
.header .logos .logo_big svg {
  width: 148px;
  height: 108px;
  fill: #6fbcbc;
}
.header .logos .logo_small {
  margin-left: 6px;
  margin-top: 12px;
}
.header .logos .logo_small svg {
  width: 102px;
  height: 24px;
}
.header .nav {
  align-self: center;
  margin-left: auto;
  margin-top: -18px;
  margin-right: 25px;
}
.header .nav__item {
  color: #6fbcbc;
  font-size: 17px;
  font-family: 'futura-futuris', sans-serif;
  font-weight: 400;
  border: 2px solid #fff;
  border-radius: 100px;
  padding: 8px 15px;
}
.header .nav__item:not(:first-of-type) {
  margin-left: 4px;
}
.header .phone {
  align-self: center;
  font-family: 'futura-futuris', sans-serif;
  color: #6fbcbc;
  font-weight: 700;
  font-size: 25px;
  margin-top: -18px;
}
.header .burger {
  position: absolute;
  display: none;
  right: 0;
  top: 0;
  width: 30px;
  height: 21px;
}
.header .burger span {
  position: absolute;
  left: 0;
  top: 0;
  bottom: 0;
  right: 0;
  margin: auto;
  width: 100%;
  height: 3px;
  background-color: #6fbcbc;
  display: block;
  border-radius: 100px;
}
.header .burger span:nth-of-type(1) {
  bottom: auto;
}
.header .burger span:nth-of-type(3) {
  top: auto;
}
@media screen and (max-width: 1200px) {
  .header__wrap {
    grid-template-columns: 238px auto 150px;
  }
  .header .logos .logo_big svg {
    width: 130px;
    height: 95px;
  }
  .header .nav__item {
    font-size: 15px;
  }
  .header .phone {
    font-size: 20px;
    margin-top: -12px;
  }
}
@media screen and (max-width: 992px) {
  .header__wrap {
    grid-template-columns: auto;
  }
  .header .nav {
    display: none;
  }
  .header .phone {
    display: none;
  }
  .header .burger {
    display: block;
  }
}
.footer__top {
