  border-top: 1px solid rgba(225, 227, 224, 0.1);
  padding: 18px 0 20px;
}
.footer__bottom {
  display: grid;
  grid-template-columns: minmax(min-content, 290px) minmax(min-content, calc(100% - 290px));
