}
@media screen and (max-width: 1200px) {
  .footer__top {
    grid-template-columns: minmax(min-content, 150px) minmax(min-content, calc(100% - 150px));
  }
  .footer__middle {
    grid-template-columns: minmax(min-content, 150px) minmax(min-content, calc(100% - 150px));
  }
  .footer__bottom {
    grid-template-columns: minmax(min-content, 150px) minmax(min-content, calc(100% - 150px));
  }
  .footer .nav__item {
    font-size: 14px;
  }
  .footer .nav__item:not(:last-of-type) {
    margin-right: 15px;
  }
  .footer .contacts__phone {
    font-size: 17px;
  }
}
@media screen and (max-width: 992px) {
  .footer__top {
    grid-template-columns: 1fr;
  }
  .footer__middle {
    padding-top: 18px;
    grid-template-columns: 1fr;
  }
  .footer__bottom {
    grid-template-columns: 1fr;
  }
  .footer .nav {
    flex-wrap: wrap;
    margin-top: 10px;
  }
  .footer .nav__item {
    margin-top: 10px;
  }
  .footer .contacts {
    margin-top: 10px;
    flex-direction: column;
    align-items: flex-start;
  }
  .footer .contacts *:not(:last-child) {
    margin-bottom: 15px;
  }
  .footer .privacy {
    flex-direction: column;
    align-items: flex-start;
    margin-top: 15px;
  }
  .footer .privacy *:not(:last-child) {
    margin-bottom: 15px;
  }
  .footer .privacy .siluet {
    margin-left: 0;
  }
}
.intro {
  position: relative;
  height: 670px;
  overflow: hidden;
}
.intro__image {
  position: absolute;
  height: 100%;
  width: 100%;
  pointer-events: none;
}
.intro__image .bg {
  object-fit: cover;
  width: 100%;
  height: 100%;
}
.intro__image .dots {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;
}
.intro__image .light {
  position: absolute;
  left: 0;
  top: 0;
  display: none;
}
.intro__image .light-block {
  position: absolute;
  left: -700px;
  top: 0;
  width: 1400px;
  height: 500px;
  border-radius: 0 30% 70% 0;
  background: radial-gradient(circle farthest-corner at 50% 20%, rgba(111, 188, 188, 0.5) 0, transparent 50%, transparent 60%);
}
.intro__content {
  position: relative;
  max-width: 560px;
  padding-top: 190px;
}
.intro__content .title {
  font-size: 100px;
  font-family: 'futura-futuris', sans-serif;
  font-weight: 700;
}
.intro__content .subtitle {
  font-family: 'futura-futuris', sans-serif;
  font-weight: 400;
  font-size: 35px;
  margin: 30px 0 74px;
  line-height: 1.2;
}
.intro__content .intro__btn {
  font-size: 25px;
  font-family: 'futura-futuris', sans-serif;
  color: #6fbcbc;
  font-weight: 400;
  border: 3px solid #fff;
  padding: 11px 75px 11px 20px;
  border-radius: 100px;
  position: relative;
}
.intro__content .intro__btn:after {
  position: absolute;
  top: 0;
  right: 7px;
  bottom: 0;
  margin: auto;
  content: '';
  background-image: url("../img/icons/play.png");
  background-size: 12px 14px;
  background-position: 59% 50%;
  background-repeat: no-repeat;
  width: 40px;
  height: 40px;
  border: 2px solid #fff;
  border-radius: 100px;
