#canvas {
  width: 100%;
  height: 600px;
}
.product {
  padding: 70px 0 90px;
}
.product__wrap {
  display: grid;
  grid-template-areas: "title content" "booklet content" "btn content";
  grid-template-columns: minmax(min-content, 30%) minmax(min-content, 70%);
  column-gap: 15px;
  align-content: start;
}
.product__title {
  align-self: start;
  font-size: 50px;
  font-weight: 400;
  color: #6fbcbc;
  margin: 128px 0 30px;
  line-height: 1.1;
  grid-area: title;
}
.product__booklet {
  align-self: start;
  margin-bottom: 20px;
  grid-area: booklet;
}
.product__btn {
  grid-area: btn;
  justify-self: start;
  align-self: start;
  font-size: 17px;
  color: #6fbcbc;
  border: 3px solid #fff;
  border-radius: 100px;
  padding: 7px 15px;
}
.product__content {
  grid-area: content;
  position: relative;
  padding-top: 59px;
  min-height: 700px;
}
.product__item {
  width: 230px;
  height: 200px;
  position: absolute;
  display: flex;
  justify-content: center;
  align-items: center;
}
.product__item .hexagon {
  position: absolute;
  z-index: -1;
  left: 0;
  top: 0;
  width: 230px;
  height: 200px;
}
.product__item .hexagon polygon {
  fill: transparent;
  stroke-width: 4;
  stroke: #6fbcbc;
  transition: 0.3s;
}
.product__item .icon {
  fill: #6fbcbc;
  transition: 0.3s;
}
.product__item .name {
  font-size: 19px;
  font-weight: 400;
  color: #fff;
  position: absolute;
  bottom: 19px;
  left: 0;
  right: 0;
  margin: 0 auto;
  line-height: 0.9;
  max-width: 110px;
  text-align: center;
}
.product__item_empty {
  opacity: 0.2;
  position: absolute;
}
.product__item_empty .hexagon polygon {
  fill: none;
}
.product__item_empty:nth-of-type(8) {
  right: 598px;
  top: -1px;
}
.product__item_empty:nth-of-type(9) {
  top: 169px;
  right: 0;
}
.product__item_empty:nth-of-type(10) {
  top: 500px;
  right: 188px;
}
.product__item:not(.product__item_empty):nth-of-type(1) {
  top: 279px;
  right: 568px;
}
.product__item:not(.product__item_empty):nth-of-type(2) {
  top: 169px;
  right: 378px;
}
.product__item:not(.product__item_empty):nth-of-type(3) {
  top: 59px;
  right: 188px;
}
.product__item:not(.product__item_empty):nth-of-type(4) {
  top: 499px;
  right: 568px;
}
.product__item:not(.product__item_empty):nth-of-type(5) {
  top: 389px;
  right: 378px;
}
.product__item:not(.product__item_empty):nth-of-type(6) {
  top: 279px;
  right: 188px;
}
.product__item:not(.product__item_empty):nth-of-type(7) {
  top: 389px;
  right: 0;
}
.product__item:not(.product__item_empty):hover .hexagon polygon {
  fill: #6fbcbc;
}
.product__item:not(.product__item_empty):hover .icon {
  fill: #fff;
}
.product-detail {
  background-color: #121e21;
  padding: 83px 0 76px;
}
.product-detail__wrap {
  display: grid;
  grid-template-areas: "title img" "wysiwyg img" "btns img";
  justify-items: start;
  grid-template-columns: minmax(min-content, 56%) minmax(min-content, 36%);
  grid-template-rows: 57px minmax(min-content, 40px) 90px;
  column-gap: 90px;
}
.product-detail__img {
  align-self: start;
  height: 410px;
  grid-area: img;
}
.product-detail__img img {
  display: block;
  object-fit: cover;
  width: 100%;
  height: 100%;
}
.product-detail__title {
  margin: 0 0 17px;
  font-weight: 700;
  color: #6fbcbc;
  font-size: 40px;
  grid-area: title;
}
.product-detail__btns {
  margin-top: 19px;
  grid-area: btns;
  display: flex;
  align-self: start;
}
.product-detail__btns a {
  display: block;
  border: 3px solid #fff;
  border-radius: 100px;
  color: #6fbcbc;
  font-size: 17px;
  position: relative;
  margin-right: 12px;
}
.product-detail__btns a.order {
  padding: 6px 49px 8px 12px;
}
.product-detail__btns a.close {
  padding: 6px 41px 8px 12px;
}
.product-detail__btns a .icon {
  position: absolute;
  fill: #6fbcbc;
  top: 0;
  bottom: 0;
  right: 15px;
  margin: auto;
}
.product-detail .wysiwig {
  grid-area: wysiwyg;
}
.product-detail .wysiwig p,
.product-detail .wysiwig ol,
.product-detail .wysiwig ul {
  font-size: 20px;
  margin: 25px 0;
}
.product-detail .wysiwig p {
  line-height: 1.4;
}
.product-detail .wysiwig a {
  color: #6fbcbc;
}
.product-detail .wysiwig ul,
.product-detail .wysiwig ol {
  list-style: none;
  padding: 0;
}
.product-detail .wysiwig ul li {
  position: relative;
  padding-left: 30px;
  margin-bottom: 16px;
}
.product-detail .wysiwig ul li:before {
  content: '';
  position: absolute;
  width: 7px;
  height: 7px;
  background-color: #6fbcbc;
  border-radius: 100px;
  left: 0;
  top: 9px;
