.advantages__wrap {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  column-gap: 40px;
  row-gap: 40px;
  grid-template-rows: repeat(2, 230px);
}
.advantages__title {
  font-size: 50px;
  color: #fff;
  font-weight: 400;
  margin: 17px 0 0;
  line-height: 1.1;
}
