}
.advantages__item:not(.advantages__item_main) .btn span {
  display: block;
  width: 100%;
  height: 2px;
  background-color: #6fbcbc;
  position: absolute;
  left: 0;
  top: 0;
  bottom: 0;
  right: 0;
  margin: auto;
