.feedback {
  padding: 109px 0 63px;
}
.feedback__title {
  margin: 0 0 24px;
  font-size: 30px;
  color: #6fbcbc;
  font-weight: 400;
}
.form__wrap {
  display: grid;
  grid-template-columns: 1fr 1fr;
  column-gap: 40px;
  grid-template-areas: "firstname comment" "phone comment";
}
.form__item {
  display: block;
  margin-bottom: 18px;
}
.form__item_firstname {
  grid-area: firstname;
}
.form__item_phone {
  grid-area: phone;
}
.form__item_comment {
  grid-area: comment;
}
.form__item-name {
  margin: 0 0 8px;
  font-size: 20px;
  color: #fff;
}
.form__item-input {
  width: 100%;
  height: 50px;
  background-color: #0b1214;
  border: 3px solid #6fbcbc;
  outline: none;
  padding: 20px;
  font-size: 17px;
  color: #fff;
  font-family: 'futura-futuris', sans-serif;
}
.form__item-input.error {
  border-color: red;
}
.form__item-comment {
  width: 100%;
  height: 150px;
  background-color: #0b1214;
  border: 3px solid #6fbcbc;
  outline: none;
  padding: 20px;
  font-size: 17px;
  color: #fff;
  font-family: 'futura-futuris', sans-serif;
}
.form__item-comment.error {
  border-color: red;
}
.form__footer {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.form__agree {
  position: relative;
  cursor: pointer;
}
.form__agree .agree {
  position: absolute;
  visibility: hidden;
  opacity: 0;
}
.form__agree .agree.error + .form__agree-text:before {
  border-color: red;
}
.form__agree .agree:checked + .form__agree-text:after {
  opacity: 1;
}
.form__agree-text {
  font-size: 17px;
  position: relative;
  padding-left: 59px;
}
.form__agree-text:before {
  content: '';
  position: absolute;
  left: 0;
  top: -12px;
  width: 40px;
  height: 40px;
  border: 3px solid #6fbcbc;
}
.form__agree-text:after {
  content: '';
  position: absolute;
  left: 9px;
  top: 0;
  width: 20px;
  height: 13px;
  background-image: url("../img/icons/agree.png");
  opacity: 0;
  transition: 0.3s;
}
.form__agree-text a {
  text-decoration: underline;
}
.form__submit {
  background-color: #6fbcbc;
  color: #fff;
  font-family: 'futura-futuris', sans-serif;
  font-size: 17px;
  border-radius: 100px;
  border: 3px solid #6fbcbc;
  padding: 9px 17px;
}
